  methods:{
    changeDetail(index){
      this.isBigPic = false;
      var that  = this;
      this.isAltShow = false;
      this.isDetailShowe = false;
      for(let i = 0; i < this.isSelected.length; i++){
        this.isSelected[i] = false;
      }
      this.isSelected[ index - 1 ] = true;
      setTimeout(function(){
        that.index1 = index - 1;
        that.index2 = index - 1;
        that.nowDetail3 = that.backDetail3[ index - 1 ];
        that.isDetailShowe = true;
      }, 100);
      this.makeSelected(index - 1);
    },

    makeSelected(index) {
      let picSet = document.querySelectorAll('.picTd');
      picSet.forEach((item, innerIndex) => {
        // item.classList.remove('selected-pic');
        if (innerIndex == index) {
          item.classList.add('selected-pic');
        }
      })
    },

    togglePic(){
      var that  = this;
      that.isDetailShowe = false;
      setTimeout(function(){
        that.isBigPic = true;
      }, 200);
    },

    generateRandom(){
      var sum = this.backPic.length;
      var that = this;
      setInterval(function(){
        if(that.randomIndex < sum  - 1 ){
          that.randomIndex++;
        }
        else {
          that.randomIndex = 0;
        }
      }, 4000)
    },
  },
}
